*{
  font-family: sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}
h2{
  background: #ddd;
  width: 600px;
  border: 2px solid #888;
  padding: 5px;
  text-align: center;
}
h3{
  border-bottom: 1px solid #888;
  width: 600px;
}
.successNotice{
  color: rgba(0,155,0,1);
  background: rgba(0,155,0,0.2);
  border: 2px solid rgba(0,155,0,1);
  padding: 16px;
  margin: 10px;
  width: 600px;
}
.errorNotice{
  color: rgba(155,0,0,1);
  background: rgba(155,0,0, 0.2);
  border: 2px solid rgba(155,0,0,1);
  padding: 16px;
  margin: 10px;
  width: 600px;
}
.addbtn{
  outline: none;
  padding: 5px;
  margin: 5px;
  background: rgba(10, 10, 255, 0.7);
  color: #fff;
  cursor: pointer;
}
.rmbtn{
  font-weight: bold;
  color: rgb(255,0,0);
  outline: none;
  background: transparent;
  margin-left: 10px;
  border: none;
  padding: 5px;
  cursor: pointer;
  font-size: 1.1em;
}
